html, body {
  background: -webkit-radial-gradient(#34495e 0, #19232d 100%);
  background: radial-gradient(#34495e 0, #19232d 100%);
  min-height: 100vh;
  font-family: "Rubik", sans-serif;
  padding: 0;
  margin: 0;
}

.description {
  color: #ecf0f1;
  font-size: 22px;
  line-height: 1.4;
  padding: 50px 0;
  text-align: center;
  width: 100%;
}
.description a,
.description a:hover,
.description a:active,
.description a:focus,
.description a:visited {
  color: #c0cdd1;
  text-decoration: none;
}
.description a:hover {
  border-bottom: 1px solid white;
}

.menu-container {
  box-shadow: 0 0px 50px rgba(0, 0, 0, 0.7);
  position: relative;
  -webkit-transform: translate(0);
  transform: translate(0);
  margin: 0 auto;
  width: 350px;
}

.menu-item,
.menu-item--active {
  height: 60px;
  width: 100%;
}

.menu-item {
  align-items: center;
  color: #ecf0f1;
  cursor: pointer;
  display: flex;
  font-size: 24px;
  justify-content: center;
  letter-spacing: 4px;
  text-align: center;
}
.menu-item.active {
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.7);
}
.menu-item:nth-of-type(1) {
  background: #f39c12;
}
.menu-item:nth-of-type(2) {
  background: #e08e0b;
}
.menu-item:nth-of-type(3) {
  background: #c87f0a;
}
.menu-item:nth-of-type(4) {
  background: #b06f09;
}
.menu-item:nth-of-type(5) {
  background: #976008;
}

.menu-item--active {
  background: #f39c12;
  border-radius: 2px !important;
  box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
  left: 50%;
  padding: 0 7px;
  position: absolute;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  -webkit-transition: all 300ms;
  transition: all 300ms;
  z-index: -1;
}


